.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@font-face {
  font-family: "RobotoMono"; 
  src: url("roboto_mono/RobotoMono-Medium.ttf") format("truetype"); 
}

@font-face {
  font-family: "RobotoMonoLight"; 
  src: url("roboto_mono/RobotoMono-Light.ttf") format("truetype"); 
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* align-items: center;
  justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.introduction {
  background-color: #171717;
  font-family: RobotoMono;
  min-height: 100vh;
  display: flex;
  flex-direction: column; 
  font-size: calc(10px + 2vmin); 
  color: white;
}

.intro-box {
  padding-left: 70px; 
  padding-top: 8%;
}

.typing-box {
  padding-left: 70px; 
}

.intro-hi {
  font-size: 90px; 
}

.intro-name {
  font-size: 80px; 
}

.intro-buttons {
  padding-top: 10%;
  font-family: RobotoMono; 
  padding-left: 70px;
  padding-bottom: 30px; 
  width: 100px; 
}

.intro-button {
  font-family: RobotoMono; 
  color: #ffffff;
  font-size: 18px; 
  padding-top: 5px;
}

.intro-button:hover {
  text-decoration: underline;
  cursor: pointer;
} 

.about {
  background-color: #092c50; 
  min-height: 100vh; 
  width: 50%; 
  float: left; 
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  justify-content: center; 
}

.right-about {
  height: 100vh; 
  width: 50%; 
  float: left; 
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  justify-content: center; 
  background-color: #fff5fa;
}

.experience {
  background-color: #4985b0; 
  min-height: 100vh; 
  width: 50%; 
  float: left; 
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  justify-content: center; 
}

.right-experience {
  height: 100vh; 
  width: 50%; 
  float: left; 
  display: flex; 
  overflow: auto;
  background-color: #092c50;
  color: #ffffff;
  text-align: center;
}

.experience-content {
  width: 80%; 
  min-height: 100%; 
  text-align: left !important;
  margin-left: 10%; 
  font-size: 20px; 
  justify-content: left; 
  align-items:left;
  margin-bottom: 50px; 
}

.right-contact {
  min-height: 100vh; 
  width: 50%; 
  float: left; 
  display: flex; 
  flex-direction: column; 
  /* align-items: center;  */
  /* justify-content: center;  */
  text-align: left !important;
  background-color: #4985b0; 
  color: white;
}

.contact {
  background-color: #ebfff5;
  min-height: 100vh; 
  width: 50%;
  float: left;
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  justify-content: center; 
}

img {
  max-width:100%; 
  height: auto; 
}

.intro-img {
  width: 70vh; 
  margin-top: 130px; 
  margin-bottom: 130px; 
}

.section-img {
  width: 40vh; 
  padding-top: 30%;
}

@font-face {
  font-family: "United"; 
  src: url("United-lgZww.ttf") format("truetype"); 
}

@font-face {
  font-family: "Gidole";
  src: url("Gidole-Regular.otf") format("opentype");
}

@font-face {
  font-family: "Domotika";
  src: url("Domotika-Regular-trial.ttf") format("truetype");
}

.student-header {
  font-family: Domotika; 
  color: #092c50; 
}
