.about-button {
  font-family: Domotika; 
  color: #ffffff;
  margin-top: 500px; 
  margin:20px; 
  font-size: 20px; 
}

.contact-button {
  color: #092c50;
  margin-top: 500px; 
  margin:20px; 
  font-size: 20px; 
}

.about-button:hover {
  text-decoration: underline;
  cursor: pointer;
}

.about-button-dark {
  font-family: United; 
  color: #092c50;
  margin-top: 500px; 
  margin:20px; 
  font-size: 20px; 
}

.about-button-dark:hover {
  text-decoration: underline;
  cursor: pointer;
}

.about-buttons-holder {
  padding-top: 200px; 
}

.up-arrow {
  width: 10px; 
  color: white;
  margin-right: 80%; 
  padding: 0px; 
  margin-top: -50px; 
}

.up-arrow:hover {
  cursor: pointer;
}

.experience-type-headers {
  background-color: #092c50;
  width: 100%;
  height: 300px; 
}

.contact-info {
  width: 80%; 
  min-height: 100%; 
  text-align: left !important;
  margin-top: 10%; 
  margin-left: 10%; 
  font-size: 20px; 
}

.contact-first-line {
  margin-bottom: 65px; 
}

.contact-second-line {
  margin-bottom: 45px; 
}

.contact-more-info {
  margin-bottom: 7px; 
}

.social-link {
  display: inline;
  width: 40px; 
  float: right;
  padding-right: 30px; 
  padding-top: 30px; 
}

a, a:link, a:visited {
  color: white;
  text-decoration: none;
}

.love-footer {
  margin-top: 70%;
  font-size: 15px; 
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
